<template>
    <article :class="knockoutclass" class="mb-3 card card--knockouts">
        <div class="card-header" v-html="name"></div>
        <table class="table table-vsm table-knockouts">
            <tbody>
                <match v-for="(match, index) in knockout.matches" :key="index" :id="knockout.name" :game="match" :gametype="'knockouts'" :locked="isLocked(match)" :betted="hasBet(match)"></match>
            </tbody>
        </table>
    </article>
</template>

<script>
    import KnockoutModel from '../Model/knockout';
    import Match from './Match';

    export default {
        props: {
            knockout: {
                type: '',
                required: true,
            },
            ownBets: {}
        },

        methods: {
            isLocked(match) {
                return new Date(match.date).getTime() < new Date().getTime();
            },

            hasBet(match) {
                let returned = false;
                if (this.ownBets)
                    Object.values(this.ownBets).forEach(bet => {
                        if (bet.game.name === match.name) {
                            returned = true;
                            return true;

                        }
                    })
                if (!returned) {
                    return false;
                }
            }
        },
        computed: {
            knockoutclass() {
                return 'card--' + this.knockout.id;
            },
            name() {
                switch (this.knockout.id) {
                    case 'round_2_loser':
                        return '<i class="bronze fa fa-2x fa-trophy"></i> ' + this.knockout.name;
                    case 'round_2':
                        return '<i class="gold fa fa-2x fa-trophy"></i> ' + this.knockout.name;
                    default:
                        return this.knockout.name;
                }
            },


        },
        components: {
            Match,
        },
    };
</script>
